<template>
    <div class="questionnaireNotFinish">
        <!-- 头部 -->
        <navigation-bar  @onLeftClick="onLeftClick" :pageName="title" >
           
       </navigation-bar>

        <!-- 问卷详情 -->
       <section class="list">
           <form id="question">
            <div class="item" v-if="isDisplayed == 0">
                <div class="questionnaireTitle">
                    <div class="leftImg">
                        <img :src="iconImg" class="iconImg">
                    </div>
                    <div class="rightCon">{{titleName}}</div>
                </div>
                <div class="questionHeader">Q{{titleNo}} : {{questName}} ({{ questionType}}) </div>
                
                <!-- 问答题 -->
                <div class="questionCon questionInput" v-if="list.QUEST_TYPE_FLAG == 0">
                    <van-cell-group>
                        <van-field
                            v-model="message"
                            rows="1"
                            autosize
                            maxlength="100"
                            type="textarea"
                            placeholder="请输入"
                        />
                 <div class="txtLimit">{{message.length}}/100</div>
                    </van-cell-group>
                </div>

                <!-- 单选 -->
                <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 1">
                    <van-radio-group v-model="radio" >
                        <van-radio  checked-color="#17B7BD" v-for="(item,index) in list.optionList" :name="item.ITEM_CODE" :key="index">{{item.ITEM_NAME}}</van-radio>
                    </van-radio-group>
                </div>

                   <!-- 多选 -->
                <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 0">
                    <div >
                        <van-checkbox-group v-model="result" >
                            <van-checkbox  checked-color="#17B7BD" v-for="(item,index) in list.optionList" :key='index' :name="item.ITEM_CODE">{{item.ITEM_NAME}}</van-checkbox>
                        </van-checkbox-group>
                    </div>
                    
                </div>

                
            </div>
            <div class="item" v-if="isDisplayed == 1">
                <div class="questionnaireTitle">
                    <div class="leftImg">
                        <img :src="iconImg" class="iconImg">
                    </div>
                    <div class="rightCon">{{titleName}}</div>
                </div>
                <div class="questionHeader">Q{{titleNo}} : {{questName}} ({{ questionType}}) </div>
                
                <!-- 问答题 -->
                <div class="questionCon questionInput" v-if="list.QUEST_TYPE_FLAG == 0">
                    <van-cell-group>
                        <van-field
                            v-model="message"
                            rows="1"
                            autosize
                            disabled
                            type="textarea"
                            placeholder="请输入"
                        />
                    </van-cell-group>
                </div>

                <!-- 单选 -->
                <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 1">
                    <van-radio-group v-model="radio" >
                        <van-radio  checked-color="#17B7BD" v-for="(item,index) in list.optionList" :name="item.ITEM_CODE" :key="index">{{item.ITEM_NAME}}</van-radio>
                    </van-radio-group>
                </div>

                   <!-- 多选 -->
                <div v-if="list.QUEST_TYPE_FLAG == 1 && list.SELECT_TYPE_FLAG == 0">
                    <div >
                        <van-checkbox-group v-model="result" >
                            <van-checkbox  checked-color="#17B7BD" v-for="(item,index) in list.optionList" :key='index' :name="item.ITEM_CODE">{{item.ITEM_NAME}}</van-checkbox>
                        </van-checkbox-group>
                    </div>
                    
                </div>

                
            </div>
           </form>
       </section>

       <section class="bottomBtn" v-if="btnFlag == 0"> 
           <submit-btn :submitName="btnName" @save='next'></submit-btn>
       </section>

       <section class="bottomBtn" v-if="btnFlag == 1"> 
           <submit-btn  @save='submit'></submit-btn>
       </section>
    </div>
</template>

<script>
import NavigationBar from '@/common/currency/NavigationBar.vue';
// import QuestionnaireItem from '@/common/servers/questionnaire/QuestionnaireItem.vue';
import SubmitBtn from '@/common/currency/SubmitBtn';
import { getQuestionNotItem,saveQuestion,savePaperReader} from '@/services/servers/question';
import { Toast } from 'vant';

export default {
    name:'questionnaireNotFinish',
      components:{
        NavigationBar,
        SubmitBtn
    },
    data(){
        return{
            title:'问卷详情',
            btnName:'下一题',
            btnFlag:0,
            isDisplayed:0,
            isNowForbid:true,
            questionType:"单选",
            message:'',
            radio:'',
            nowNum:1,
            isShow:true,
            list:{},
            result:[],
            item:{},
            con:{},
            questData:[],
            titleName:'',
            titleNo:'',
            questName:'',
            btnFlag:0,
            isSaveing:false,
            iconImg:require('@/assets/images/home/question/question.png'),
        }
    },
    mounted(){
        this.item = this.$route.query
        this.initData();
        this.isRead();

    },
    methods:{
        onLeftClick(){
            this.$store.dispatch("changeActiveFlag", 0);
            this.$router.go(-1);
        },
        isRead(){
            let data = {
                "PUBLISH_CODE":this.item.PUBLISH_CODE,
                "PAPER_CODE":this.item.PAPER_CODE,
                "USER_CODE":this.$userInfo.OWNER_CODE || '',
                "USER_NAME":this.$userInfo.OWNER_NAME || '',
                "USER_NICK_NAME":this.$userInfo.OWNER_NAME || '',
                "ROOM_CODE":this.$userInfo.ROOM_CODE || ''
            };
            data = {data:JSON.stringify(data)};
            savePaperReader(data).then(
                res => {

                },
                err => {

                }
            )

        },

        next(){
           let linkFlag = 0;
           let questCode;
           let content = {};
           console.log(this.con)
           if(this.questionType == "单选"){
               if(this.radio <= 0){
                   Toast('请做出您的选择...')
                   return
               }
               let questItem = {};
               this.con.optionList.forEach((item) => {
                   if(item.ITEM_CODE == this.radio){
                       linkFlag = item.LINK_QUEST_FLAG;
                       questCode = item.LINK_QUEST_CODE;
                   }
               })
               questItem.QUEST_CODE = this.con.subjectMap.QUEST_CODE;
               questItem.ITEM_CODE = this.radio||'';
               questItem.CONTENT = '';
               questItem.POINT= 0;
               this.questData.push(questItem);
              
           }else if(this.questionType == "多选"){
                if(this.result <= 0){
                    Toast('请做出您的选择...')
                    return;
                }
                if(this.result.length > this.con.subjectMap.MUTI_CNT){
                    Toast('最多选择'+this.con.subjectMap.MUTI_CNT+'个选项')
                    return;
                }
                this.con.optionList.forEach((item) => {
                   if(item.ITEM_CODE == this.result[0]){
                       linkFlag = item.LINK_QUEST_FLAG;
                       questCode = item.LINK_QUEST_CODE;
                   }
               })
               for(let i=0;i<this.result.length;i++){
                   let questItem = {};
                   questItem.QUEST_CODE = this.con.subjectMap.QUEST_CODE;
                   questItem.ITEM_CODE = this.result[i];
                   questItem.CONTENT = '';
                   questItem.POINT= 0;
                   this.questData.push(questItem);
                 
               }
           }else{
               if(this.message.length <= 0){
                    Toast('请输入您的意见...')
                   return;
               }
               /* 问答题跳转功能 */
               if(this.con.optionList.length > 0){
                linkFlag = this.con.optionList[0].LINK_QUEST_FLAG;
                questCode = this.con.optionList[0].LINK_QUEST_CODE;
               }
                let questItem = {};
                questItem.QUEST_CODE =  this.con.subjectMap.QUEST_CODE;
                questItem.CONTENT = this.message;
                this.questData.push(questItem);
                
           }
          
           console.log(this.questData);
           let data = {};
           if(linkFlag == 0){
               data.PAPER_CODE = this.item.PAPER_CODE;
            data.SORT_NO = this.nowNum;
            
           }else{
               data.QUEST_CODE = questCode;
           }
            this.isShow = false;
           data = {data:JSON.stringify(data)};
             getQuestionNotItem(data).then(
                 res =>{
                    if(res.optionList.length == 0 && !res.subjectMap){
                        Toast('请提交您的问卷，感谢您的参与，祝您生活愉快!');
                        this.btnFlag = 1;
                        this.isDisplayed = 1;
                        return
                    }else{
                        this.radio = '';
                        this.result = [];
                        this.message = '';
                    }
                    this.con = res;
                    this.nowNum = this.con.subjectMap.SORT_NO + 1;
                    this.isShow = false;
                    this.isShow = true;
                    this.titleName = this.con.subjectMap.PAPER_NAME;
                    this.titleNo = this.con.subjectMap.SORT_NO;
                    this.questName = this.con.subjectMap.QUEST_NAME;
                    this.list.optionList = res.optionList;
                    this.list.QUEST_TYPE_FLAG = res.subjectMap.QUEST_TYPE_FLAG;
                    this.list.SELECT_TYPE_FLAG = res.subjectMap.SELECT_TYPE_FLAG;
                    this.$forceUpdate();
                    this.isTypeQus()
                     
                 },
                 err =>{

                 }
             )
        },
        
        initData(){
            let data = {
                    'PUBLISH_CODE':this.item.PUBLISH_CODE,
                    'QUEST_CODE':this.item.QUEST_CODE,
                    'PAPER_CODE':this.item.PAPER_CODE,
                    'PUBLISH_TYPE':this.item.PUBLISH_TYPE,
                    "SORT_NO":this.nowNum,
                    "TYPE_FLAG":1,
                    "ENROL_CODE":"",
            };
             this.isShow = false;
             data = {data:JSON.stringify(data)};
             getQuestionNotItem(data).then(
                 res =>{
                        this.con = res;
                        this.isShow = true;
                        this.nowNum = this.con.subjectMap.SORT_NO + 1;
                        this.titleName = this.con.subjectMap.PAPER_NAME;
                        this.titleNo = this.con.subjectMap.SORT_NO;
                        this.questName = this.con.subjectMap.QUEST_NAME;
                        this.list.optionList = res.optionList;
                        this.list.QUEST_TYPE_FLAG = res.subjectMap.QUEST_TYPE_FLAG;
                        this.list.SELECT_TYPE_FLAG = res.subjectMap.SELECT_TYPE_FLAG;
                        this.isTypeQus()
                 },
                 err =>{

                 }
             )
            
        },

        // 提交
        submit(){
            if(this.isSaveing){
                return;
            }
            let data = {
                'PUBLISH_CODE':this.item.PUBLISH_CODE,
                'PAPER_CODE':this.item.PAPER_CODE,
                "USER_CODE":this.$userInfo.OWNER_CODE || '',
                "USER_NICK_NAME":this.$userInfo.OWNER_NICK_NAME || '',
                "USER_NAME":this.$userInfo.OWNER_NAME || '',
                "ROOM_CODE":this.$userInfo.ROOM_CODE || '',
                "AREA_CODE":this.$userInfo.AREA_CODE || '',
                "ENROL_CODE":""
            }

            let quesData = {
                "PAPER_CODE":this.item.PAPER_CODE,
                "SORT_NO":this.nowNum,
                "moreQuestion":1
            }
            quesData = {data:JSON.stringify(quesData)};
            getQuestionNotItem(quesData).then(
                 res =>{
                       
                 },
                 err =>{

                 }
             )

            this.isSaveing = true;
            let reqData = {data:JSON.stringify(data),questData:JSON.stringify(this.questData)};
            saveQuestion(reqData).then(
                res => {
                    this.isSaveing = false;
                    this.$store.dispatch("changeActiveFlag", 0);

                    if(res.success && res.data && res.data != "")
                        this.$toast("恭喜问卷调查获取" + res.data + "积分");

                    this.$router.go(-1);
                },
                err => {
                    this.isSaveing = false;
                }
            )
            
        },

        // 题型判断
        isTypeQus(){
          if(this.list.QUEST_TYPE_FLAG == 1 && this.list.SELECT_TYPE_FLAG == 1){
              this.questionType ="单选"
          }else if(this.list.QUEST_TYPE_FLAG == 1 && this.list.SELECT_TYPE_FLAG == 0){
              this.questionType ="多选"
          }else if(this.list.QUEST_TYPE_FLAG == 0){
              this.questionType = "问答"
          }
        }
    }
}
</script>

<style >
.questionnaireNotFinish{
    width: 100%;
    height: 100%;
    display: flex;
    overflow: hidden;
    flex-direction: column;
    background-color: #f9f9f9;
}
.questionnaireNotFinish .list{
    margin: 9px 0 0 0;
    padding-bottom: 12px;
    background-color: #ffffff;
    margin: 15px 15px 0 15px;
    box-shadow: 0 0 6px 0 rgba(219,219,219,0.50);
    border-radius: 5px;
    overflow-y: auto;
}
.questionnaireNotFinish .questionnaireTitle{
    width: calc(100% - 24px);
    /* height: 24px; */
    padding: 12px;
    display: flex;
    border-bottom: 1px solid #f8f8f8
}
.questionnaireNotFinish .leftImg{
    height: 24px;
    width: 24px;
    margin-right: 12px
}
.questionnaireNotFinish .leftImg img{
    display: block;
    width: 100%;
    height: 100%;
}
.questionnaireNotFinish .rightCon{
    flex: 1;
    /* height: 24px; */
    line-height: 24px;
    font-size: 14px;
    word-break: break-word;
}
.questionnaireNotFinish .bottomBtn{
    width: 100%;
    height: 70px;
    line-height: 70px;
    /* overflow: hidden; */
    margin-top: 7px;
}
.questionnaireNotFinish .questionHeader{
    /* height: 20px; */
    font-size: 14px;
    width: calc(100% - 24px);
    padding: 12px;
}
.questionnaireNotFinish .questionCon{
    height: auto;
   
}
.questionnaireNotFinish .questionInput{
    min-height: 120px;
}
.questionnaireNotFinish .van-hairline--top-bottom::after{
    border: none;
    border-width: 0;
}
.questionnaireNotFinish .van-cell-group{
    border: none;
    border-width: 0;
}
.questionnaireNotFinish .van-cell{
    padding-bottom: 0;
}
.questionnaireNotFinish .van-cell:not(:last-child)::after{
    border-bottom: none;
}
.questionnaireNotFinish .txtLimit{
    height:10px;
    line-height:10px;
    font-size:10px;
    border:none;
    text-align:right;
    margin: 0 16px;
    padding:10px 5px;
    background:#f8f8f8;
    color: #9A9A9A;
}
</style>